"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { supabaseClientInstance } from "../lib/supabaseClient";

export default function LoginPage() {
  const router = useRouter();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isLogin, setIsLogin] = useState(true); // ログイン or 新規登録の切り替え
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError("");

    if (isLogin) {
      // ✅ ログイン処理
      const { error } = await supabaseClientInstance.auth.signInWithPassword({ email, password });

      if (error) {
        setError(error.message);
      } else {
        router.push("/mypage"); // ログイン成功時にマイページへ遷移
      }
    } else {
      // ✅ 新規登録処理
      const { error } = await supabaseClientInstance.auth.signUp({ email, password });

      if (error) {
        setError(error.message);
      } else {
        alert("確認メールを送信しました！メールを確認してください。");
      }
    }

    setLoading(false);
  };

  return (
    <div className="flex min-h-screen items-center justify-center bg-gray-900 text-white">
      <form onSubmit={handleSubmit} className="w-full max-w-sm space-y-4 p-6 bg-gray-800 rounded-lg shadow-md">
        <h2 className="text-xl font-bold text-center">{isLogin ? "ログイン" : "新規登録"}</h2>
        {error && <p className="text-red-500 text-sm">{error}</p>}

        <input
          type="email"
          placeholder="メールアドレス"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          className="w-full p-2 rounded bg-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
          required
        />
        <input
          type="password"
          placeholder="パスワード"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          className="w-full p-2 rounded bg-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
          required
        />

        <button
          type="submit"
          disabled={loading}
          className="w-full p-2 bg-blue-500 hover:bg-blue-600 rounded text-white font-bold disabled:bg-gray-600"
        >
          {loading ? "処理中..." : isLogin ? "ログイン" : "新規登録"}
        </button>

        <p className="text-center text-sm mt-2">
          {isLogin ? "アカウントがないですか？" : "すでにアカウントをお持ちですか？"}{" "}
          <button
            type="button"
            className="text-blue-400 hover:underline"
            onClick={() => setIsLogin(!isLogin)}
          >
            {isLogin ? "新規登録はこちら" : "ログインはこちら"}
          </button>
        </p>
      </form>
    </div>
  );
}